import { FC, useState, useRef, useLayoutEffect, PropsWithoutRef } from 'react';

import {
  EmptyState,
  Flex,
  ForwardProps,
  Grid,
  Icon,
  registerIcon,
  Text
} from '@pega/cosmos-react-core';
import { StyledPageIcon } from '@pega/cosmos-react-core/lib/components/PageTemplates/PageTemplates';
import * as moveRightSolidIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/move-right-solid.icon';
import * as moveLeftSolidIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/move-left-solid.icon';

import { EmailManagerProps } from './Email.types';
import {
  StyledEmailManagerContainer,
  StyledGridContainer,
  StyledHeader,
  StyledList
} from './Email.styles';

registerIcon(moveRightSolidIcon, moveLeftSolidIcon);

const EmailManager: FC<EmailManagerProps & ForwardProps> = ({
  header,
  list,
  emailCaseDetails,
  ...restProps
}: PropsWithoutRef<EmailManagerProps>) => {
  const headerRef = useRef<HTMLDivElement>(null);
  const [headerOffsetHeight, setHeaderOffsetHeight] = useState(0);

  useLayoutEffect(() => {
    setHeaderOffsetHeight(headerRef?.current?.offsetHeight || 0);
  }, []);

  return (
    <StyledEmailManagerContainer {...restProps}>
      {header && (
        <Flex
          container={{ alignItems: 'center', pad: [0, 0, 0, 2], gap: 1 }}
          as={StyledHeader}
          ref={headerRef}
        >
          {header.icon && (
            <StyledPageIcon container={{ alignItems: 'center', justify: 'center' }}>
              <Icon name={header.icon} />
            </StyledPageIcon>
          )}
          <Text variant='h1'>{header.title}</Text>
        </Flex>
      )}
      <Grid
        as={StyledGridContainer}
        container={{
          cols: 'minmax(20rem, 1fr) minmax(0, 3fr)',
          gap: 2
        }}
        style={{ '--headerOffsetHeight': `${headerOffsetHeight}px` }}
        item={{ area: 'container' }}
        hasHeader={!!header}
      >
        <StyledList>{list}</StyledList>
        <div>{emailCaseDetails || <EmptyState />}</div>
      </Grid>
    </StyledEmailManagerContainer>
  );
};

export default EmailManager;
